.form {
  display: flex;
  flex-direction: column;

  padding: 0 0 48px;

  grid-column: 1/3;

  @include onTablet {
    grid-column: 1/-1;
    padding: 0 0 80px;
  }

  @include onDesktop {
    grid-column: 1 / 7;
    padding: 0 0 0;
  }

  &__input {
    margin: 0 0 16px;
    padding: 0 0 0 16px;

    height: 48px;

    border-radius: 8px;
    border: 1px solid transparent;
    background-color: #f8f8fa;

    transition: color 0.3s, border-color 0.3s;

    &::placeholder {
      font-size: 14px;
    }

    &:hover {
      border-color: #dbdbdb;

      &:hover::placeholder {
        color: $color--black;
      }
    }
  }
  &__message {
    margin: 0 0 32px;
    padding: 14px 16px;

    height: 148px;

    font-family: inherit;

    border-radius: 8px;
    background-color: #f8f8fa;

    border: 1px solid transparent;

    resize: none;

    transition: color 0.3s linear, border-color 0.3s;

    &::placeholder {
      font-size: 14px;
    }

    &:hover {
      border-color: #dbdbdb;
    }

    &:hover::placeholder {
      color: $color--black;
    }

    &:focus {
      border-color: #f98921;
    }
  }

  &__button {
    width: 100%;

    height: 56px;

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 8px;
    border: 2px solid #F98921;
    background-color: #F98921;

    color: #fff;
    
    font-size: 16px;
    font-weight: 700;
    line-height: 27px;
    
    transition: all 0.3s linear;
    cursor: pointer;
    
    
    
    &:hover {
      color: #fff;
      background-color:  $color--black;
      border-color: $color--black;
    }

    &:focus {
      color: #fff;
      background-color: #010810;
      
    }
  }
}
